@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.fade-in {
  animation: fadeIn 0.4s ease-out;
}
.spinner {
  width: 28px;
  height: 28px;
  border: 3px solid var(--border);
